.headerContent {
  font-family: 'Lobster', sans-serif, cursive;
  font-size: 30px;
  position: relative;
  display: inline-block;
  padding-top: 20px;
}

.headerContent::after {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #0366d6;
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%) scaleX(0);
      -ms-transform: translateX(-50%) scaleX(0);
          transform: translateX(-50%) scaleX(0);
  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: -webkit-transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  -o-transition: transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
  transition: transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s, -webkit-transform 0.2s cubic-bezier(0.28, 0, 0.18, 1) 0.2s;
}

a:hover .headerContent::after {
  -webkit-transform: translateX(-50%) scaleX(1);
      -ms-transform: translateX(-50%) scaleX(1);
          transform: translateX(-50%) scaleX(1);
}

a:hover {
  color: inherit;
}


